.user-comment {
  padding: 0;
  padding-bottom: 23px;
  border: none;

  @media (min-width: 1440px) {
    padding-bottom: 72px;
  }
}

.user-comment__container {
  margin-bottom: 38px;

  @media (min-width: 1440px) {
    margin-bottom: 77px;
  }
}

.user-comment__wrapper {
  height: 150px;
  padding: 0 20px;

  @media (min-width: 768px) {
    height: 175px;
    padding: 51px 70px 40px;
  }

  @media (min-width: 1440px) {
    padding: 0 110px;
  }
}

.user-comment__textarea {
  box-sizing: border-box;
  padding: 14px 14px 14px 15px;
  width: 100%;
  height: 100%;
  resize: none;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  border: 1px solid #e7e7e7;

  &:hover {
    border: 1px solid #cdcdcd;
  }

  &:focus {
    outline: 2px solid #444444;
    outline-offset: -2px;
  }

  @media (min-width: 768px) {
    padding: 10px 19px;
    font-size: 20px;
    line-height: 30px;
  }
}
